<template>
  <v-row justify="center" v-show="open">
    <v-dialog :value="open" fullscreen hide-overlay transition="dialog-bottom-transition">
      <v-card>
        <v-toolbar dark color="primary">
          <v-btn icon dark @click="closeTerminal">
            <v-icon>mdi-close</v-icon>
          </v-btn>
          <v-toolbar-title>Debugging Terminal</v-toolbar-title>
          <v-spacer></v-spacer>
          <v-toolbar-items>
            <v-btn dark text @click="closeTerminal">Close</v-btn>
          </v-toolbar-items>
        </v-toolbar>
        <div id="term">
          <!-- Element for BLE Terminal -->
        </div>
      </v-card>
    </v-dialog>
  </v-row>
</template>

<script>
import { blx } from '../../blx_api/blx'
import '../../blx_api/FileSaver'
export default {
  name: 'Terminal',

  mounted () {},
  data () {
    return {
      notifications: false,
      sound: true,
      widgets: false
    }
  },
  watch: {
    open (newVal, oldVal) {
      if (newVal !== oldVal) {
        if (newVal === true) {
          this.$nextTick(() => {
            blx.setTerminal('term')
          })
        } else {
          // blx.setTerminal('term')
        }
      }
    }
  },
  props: ['open', 'closeTerminal']
}
</script>

