<template>
  <v-app>
    <v-app-bar app color="primary" dark flat>
      <div
        class="d-flex align-center flex-shrink-0"
        @dblclick="openTerminal"
        @click="doubleClick($event)"
      >
        <v-icon class="mr-2">mdi-bluetooth</v-icon>
        <h1 class="text-h6" style="font-weight: 900;">BLX-PROTOTYPE</h1>
      </div>

      <v-spacer></v-spacer>

      <!-- <v-btn text class="ml-auto mr-n4" @click="installApp" v-if="notInstalled">
        <v-icon class="mr-2">mdi-arrow-down-bold-circle</v-icon>
        <span class="mr-2 d-none d-sm-block">Install App</span>
      </v-btn>-->
      <v-btn color="secondary" class="primary--text" @click="openTerminal">
        <v-icon>mdi-console</v-icon>
      </v-btn>
    </v-app-bar>

    <v-main>
      <v-snackbar :value="$store.state.snackbar.open" top :color="$store.state.snackbar.logLevel">
        {{ $store.state.snackbar.notification }}
        <template v-slot:action="{ attrs }">
          <v-btn color="white" text v-bind="attrs" @click="$store.commit('setSnackbar', {open:false})">Close</v-btn>
        </template>
      </v-snackbar>
      <v-fade-transition>
        <v-alert
          v-if="!checkBluetoothAvailability && !bluetoothAvailable"
          border="right"
          class="ma-4"
          elevation="1"
          type="error"
        >Bluetooth is not supported by your browser. Apple iOS does not support Web-Bluetooth yet. This App works best with Google Chrome.</v-alert>
        <router-view v-else class="fill-height" :isBluetoothAvailable="bluetoothAvailable" />
      </v-fade-transition>
    </v-main>
    <Terminal :open="terminalOpen" :closeTerminal="() => {terminalOpen = false}"></Terminal>
    <v-footer app color="secondary">
      <v-spacer></v-spacer>
      <div>&copy; {{ new Date().getFullYear() }}</div>
    </v-footer>
  </v-app>
</template>

<script>
import Terminal from './components/Terminal.vue'
export default {
  name: 'App',

  components: { Terminal },

  data: () => ({
    terminalOpen: false,
    notInstalled: true,
    checkBluetoothAvailability: true,
    bluetoothAvailable: false,
    snackbar: true,
    text: 'My timeout is set to 2000.',
    timeout: 2000
  }),

  created () {
    if (typeof window.navigator.bluetooth === 'undefined') {
      this.checkBluetoothAvailability = false
      this.bluetoothAvailable = false
    }

    navigator.bluetooth
      .getAvailability()
      .then(isBluetoothAvailable => {
        this.bluetoothAvailable = isBluetoothAvailable
      })
      .finally(() => {
        this.checkBluetoothAvailability = false
      })

    if ('onavailabilitychanged' in navigator.bluetooth) {
      navigator.bluetooth.addEventListener('availabilitychanged', function (
        event
      ) {
        this.bluetoothAvailable = event.value
        console.log(
          `> Bluetooth is ${event.value ? 'available' : 'unavailable'}`
        )
      })
    }
  },

  methods: {
    doubleClick (ev) {
      ev.preventDefault()
      if (ev.target.dataset.double) {
        this.openTerminal()
      } else {
        ev.target.dataset.double = true
        setTimeout(() => {
          delete ev.target.dataset.double
        }, 1000)
      }
    },
    openTerminal () {
      this.terminalOpen = true
    },
    installApp () {
      this.notInstalled = false
    }
  }
}
</script>

<style>
#app {
  background-color: #fcfcfc;
  color: white;
}
</style>
