<template>
  <div>
    <v-toolbar class="blue-grey lighten-5" elevation="1" dense style="position: fixed;" width="100%">
      <v-btn @click="goBack" icon>
        <v-icon>mdi-arrow-left</v-icon>
      </v-btn>

      <v-toolbar-title>
        <span v-if="device" class="hidden-xs-only">Device</span>
        {{device && device.name}}
        <span v-if="device" class="grey--text d-none d-md-inline">[{{device && device.id}}]</span>
      </v-toolbar-title>

      <v-spacer></v-spacer>

      <v-btn icon>
        <v-icon>mdi-settings</v-icon>
      </v-btn>
      <v-btn @click="removeDevice" icon color="error">
        <v-icon>mdi-delete</v-icon>
      </v-btn>
    </v-toolbar>
    <v-container class="device">
      <h5 class="primary--text text-left" style="margin-top:50px;">Last Log</h5>
      <pre class="primary--text">
        {{device && device.data}}
      </pre>
      <v-card>
        <v-card-title>
          <v-btn disabled>Download Log</v-btn>
          <v-spacer></v-spacer>
          <v-text-field
            v-model="search"
            append-icon="mdi-magnify"
            label="Search"
            single-line
            hide-details
          ></v-text-field>
        </v-card-title>
        <v-data-table :headers="headers" :items="measurements" :search="search"></v-data-table>
      </v-card>
    </v-container>
  </div>
</template>

<script>
export default {
  data: () => ({
    search: '',
    headers: [
      {
        text: 'Date / Time',
        align: 'start',
        value: 'dateTime'
      },
      { text: 'Temperature (Â°C)', value: 'temp' },
      { text: 'Pressure (hPa)', value: 'pressure' },
      { text: 'Battery (V)', value: 'battery' }
    ],
    measurements: [
      {
        dateTime: new Date(),
        temp: 32.5,
        pressure: 12.2,
        battery: 2.2
      },
      {
        dateTime: new Date(),
        temp: 30.0,
        pressure: 11.2,
        battery: 2.2
      },
      {
        dateTime: new Date(),
        temp: 30.5,
        pressure: 10.5,
        battery: 2.2
      },
      {
        dateTime: new Date(),
        temp: 28.3,
        pressure: 12.2,
        battery: 2.2
      },
      {
        dateTime: new Date(),
        temp: 17.5,
        pressure: 12.2,
        battery: 2.2
      },
      {
        dateTime: new Date(),
        temp: 32.5,
        pressure: 12.2,
        battery: 2.2
      }
    ]
  }),
  methods: {
    goBack () {
      this.$router.go(-1)
    },
    removeDevice () {
      if (window.confirm('Do you really want to remove this device?')) {
        this.$store.commit('removeDevice', this.device.id)
        this.goBack()
      }
    }
  },
  computed: {
    device () {
      return this.$store.state.devices.find(
        el => el.id === this.$route.params.id
      )
    }
  }
}
</script>

