<template>
  <v-row justify="center">
    <v-dialog :value="open" persistent>
      <v-card>
        <v-card-title class="headline">Connected to Device: {{device && device.name}}</v-card-title>
        <v-card-text>
          <v-row dense class="pb-4">
            <v-col class="py-1" xs="12" sm="4" cols="12">
              <v-text-field
                background-color="blue-grey lighten-5"
                dense
                hide-details
                readonly
                label="Temp."
                placeholder="32.5 Â°C"
                filled
              ></v-text-field>
            </v-col>
            <v-col class="py-1" xs="12" sm="4" cols="12">
              <v-text-field
                background-color="blue-grey lighten-5"
                dense
                hide-details
                readonly
                label="Pressure"
                placeholder="x hPa"
                filled
              ></v-text-field>
            </v-col>
            <v-col class="py-1" xs="12" sm="4" cols="12">
              <v-text-field
                background-color="blue-grey lighten-5"
                dense
                hide-details
                readonly
                label="Battery"
                placeholder="2.2 V"
                color="primary"
                append-outer-icon="mdi-refresh"
                @click:append="alert('test')"
                filled
              ></v-text-field>
            </v-col>
            <v-btn
              big
              depressed
              block
              class="my-5"
              color="accent"
              :disabled="loadingData"
              :loading="loadingData"
              @click="fetchData"
            >Fetch Data from Logger</v-btn>
            <!-- <v-btn big text block disabled>Set Parameters</v-btn>
            <v-btn big text block color="error">Delete Data from Logger</v-btn>-->
          </v-row>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="error" text @click="closePopup">
            <v-icon class="mr-1">mdi-delete</v-icon><span class="d-none d-sm-block">Delete Data on Logger</span>
          </v-btn>
          <v-btn color="grey" text @click="closePopup">
            <v-icon class="mr-1">mdi-settings</v-icon><span class="d-none d-sm-block">Change Parameters</span>
          </v-btn>
          <v-btn color="primary" text @click="closePopup">Close</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </v-row>
</template>

<script>
import { blx } from '../../blx_api/blx'
import '../../blx_api/FileSaver'
export default {
  data: () => ({
    loadingData: false
  }),
  mounted () {},
  methods: {
    closePopup () {
      blx.userSendCmd('.d')
      this.close()
    },
    async fetchData () {
      try {
        this.loadingData = true
        await blx.userSendCmd('.g data.edt')
        const buf = await blx.getInfileBytebuf()
        var dataStr = new TextDecoder('utf-8').decode(buf)
        const dev = {
          name: this.device.name,
          id: this.device.id,
          log: [],
          lastFetch: new Date().toISOString(),
          data: dataStr
        }
        this.$store.commit('upsertDevice', dev)
        this.closePopup()
        this.$router.push('/device/' + dev.id)
      } catch (error) {
      } finally {
        this.loadingData = false
      }
    }
  },
  props: ['close', 'open', 'device']
}
</script>

