<template>
  <v-container>
    <v-row class="text-center">
      <v-col>
        <v-card flat class="mx-auto" tile>
          <v-list outlined class="mb-2 py-0">
            <v-progress-linear indeterminate :active="connecting" color="primary darken-2"></v-progress-linear>
            <v-list-item
              two-line
              :ripple="!isBluetoothAvailable"
              :disabled="!isBluetoothAvailable"
              @click="openConnectionPopup"
              class="accent--text font-weight-black"
              color="accent"
            >
              <v-list-item-avatar>
                <v-icon class="accent white--text">mdi-plus</v-icon>
              </v-list-item-avatar>
              <v-list-item-content>
                <v-list-item-title class="accent--text font-weight-black text-left">Live Connection</v-list-item-title>
                <v-list-item-subtitle
                  class="accent--text font-weight-regular text-left"
                >Search for a new Bluetooth device and open a live connection</v-list-item-subtitle>
              </v-list-item-content>
            </v-list-item>
            <v-divider v-show="devices.length > 0"></v-divider>
            <v-subheader v-show="devices.length > 0">DATA STORE</v-subheader>
            <v-list-item-group v-model="selectedDevice" color="primary" v-show="devices.length > 0">
              <v-list-item @dblclick="openDevice($event,i)" v-for="(device, i) in devices" :key="i">
                <v-list-item-icon>
                  <v-icon>mdi-bluetooth</v-icon>
                </v-list-item-icon>
                <v-list-item-content v-if="device">
                  <v-list-item-title class="text-left" v-text="device.name"></v-list-item-title>
                </v-list-item-content>
              </v-list-item>
            </v-list-item-group>
          </v-list>
        </v-card>
        <v-fade-transition>
          <v-row dense v-if="typeof selectedDevice === 'number'">
            <v-col cols="8" sm="10">
              <v-btn @click="readDeviceData" color="accent" depressed block>Read data</v-btn>
            </v-col>
            <v-col cols="4" sm="2">
              <v-btn @click="deleteDevice" block depressed color="error">
                <v-icon>mdi-delete</v-icon>
              </v-btn>
            </v-col>
          </v-row>
        </v-fade-transition>
      </v-col>
    </v-row>
    <connection-popup
      v-if="connectionPopup"
      :device="device"
      :open="connectionPopup"
      :close="closeConnectionPopup"
    />
  </v-container>
</template>

<script>
import { blx } from '../../blx_api/blx'
import ConnectionPopup from '../components/ConnectionPopup.vue'
const NUS_SERVICE_UUID = '5c170001-b5a3-f393-e0a9-a37f42997c22'
const NUS_DATA_CHARA_WRITE = '5c170002-b5a3-f393-e0a9-a37f42997c22'
const NUS_DATA_CHARA_READ_NOTI = '5c170003-b5a3-f393-e0a9-a37f42997c22'

export default {
  name: 'Landing',

  data: () => ({
    selectedDevice: null,
    connectionPopup: false,
    connecting: false,
    device: null
  }),
  computed: {
    devices () {
      return this.$store.state.devices
    }
  },
  components: { ConnectionPopup },
  methods: {
    async openConnectionPopup () {
      try {
        this.connecting = true
        await blx.userSendCmd('.c')
        this.device = blx.getDevice()
        this.connectionPopup = true
      } catch (error) {
        // TODO: API-Improvement: Comparing strings is not nice. Lets try to find something better here.
        if (error === 'ERROR(Cmd): Already connected!') {
          this.device = blx.getDevice()
          this.connectionPopup = true
        } else if (error === 'ERROR(Discover): NotFoundError: User cancelled the requestDevice() chooser.') {
          // usual action. do nothing
        } else {
          this.$store.dispatch('notify', {
            notification: error,
            logLevel: 'error'
          })
          console.error(error)
        }
      } finally {
        this.connecting = false
      }
    },
    closeConnectionPopup () {
      this.connectionPopup = false
    },
    deleteDevice () {
      this.$store.commit('removeDevice', this.selectedDevice)
    },
    openDevice (ev, i) {
      this.$router.push('device/' + this.devices[i].id)
    },
    async readDeviceData () {
      this.$router.push('device/' + this.devices[this.selectedDevice].id)
    },
    async addBleDevice () {
      if (!this.isBluetoothAvailable) return
      try {
        const device = await navigator.bluetooth.requestDevice({
          filters: [{ services: [NUS_SERVICE_UUID] }]
        })
        const storedDevice = {
          name: device.name,
          id: device.id,
          log: [],
          lastSeen: new Date().toISOString(),
          lastFetch: null,
          data: []
        }
        this.$store.commit('setCurrentServer', device)
        this.$store.commit('upsertDevice', storedDevice)
        this.$router.push('device/' + device.id)
      } catch (error) {
        this.$store.dispatch('notify', {
          notification: error,
          logLevel: 'error'
        })
        console.warn(error)
      }
    }
  },
  props: ['isBluetoothAvailable']
}
</script>

<style>
.v-list-item.v-list-item--disabled * {
  color: gray !important;
}

.v-list-item.v-list-item--disabled {
  background-color: #eeeeee;
}
</style>
